@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

    :root {
      --background-color: #ddd;
      --primary-color: #ee0000;
      --container-color: #fff;
    }

    * {
      box-sizing: border-box;
      margin: 0;
      padding: 0;
    }

    body, html {
      height: 100%;
      margin: 0;
      display: flex;
      flex-direction: column;
      font-size: 1rem;
      line-height: 1.6;
      font-family: "Poppins", sans-serif;
      background-color: var(--background-color);
    }

    .header {
      background-color: var(--primary-color);
      color: #fff;
      padding: 10px;
      position: relative;
    }

    .btn-menu {
      position: absolute;
      top: 35px;
      left: 20px;
      font-size: 2rem;
      background-color: transparent;
      border: none;
      color: var(--container-color);
    }

    .btn-menu:hover {
      background-color: var(--primary-color);
      cursor: pointer;
    }

    h1, h2 {
      text-align: center;
      padding: 10px;
      margin: 10px 0;
    }

    h1 a {
      color: #fff;
      text-decoration: none;
      margin-left: 30px;
    }

    @media (min-width: 1200px) {
      h1 a {
        font-size: 3rem;
      }
    }

    p {
      padding: 20px 0;
    }

    .navbar {
      padding: 10px;
      border-top: 3px solid #fff;
      display: none;
    }

    .navbar-list {
      display: flex;
      flex-direction: column;
      padding: 10px;
      list-style: none;
    }

    .navbar-item {
      padding: 5px;
      font-weight: 600;
      text-align: center;
    }

    .navbar-link {
      font-size: 1.5rem;
      color: #fff;
      text-decoration: none;
    }

    .navbar-link:hover {
      color: var(--container-color);
      text-decoration: underline;
    }

    @media (min-width: 1200px) {
      .navbar {
        display: block;
        border-top: 3px solid #fff;
      }    

      .navbar-list {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
      }

      .btn-menu {
        display: none;
      }
    }

    .show_menu {
      display: block;
    }

main {
  flex: 1;
  margin: 50px 20px;
}

.main__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-inline: auto;
}

@media (min-width: 800px) {
  .main__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 25px;
  }

  .categoria__article {
    flex-basis: 375px;
  }
}

@media (min-width: 1200px) {
  .main__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 25px;
  }

  .main__container-index,
  .main__container-presentacion,
  .main__container-alcala {
    width: 80%;
  }

  .categoria__article {
    flex-basis: 550px;
  }
}

h3 {
  text-align: center;
  text-transform: uppercase;
}

img {
  display: block;
  max-width: 100%;
  margin: 20px 0;
  margin-inline: auto;
  border-radius: 5px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.4);
}

figcaption {
  text-align: center;
  font-size: .8rem;
}

.categoria__image {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  object-fit: cover;
}

.categoria-link {
  margin-inline: center;
}

article {
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  margin: 30px 0px;
  border-radius: 5px;
  box-shadow: 2px 10px 10px #666;
  background-color: #fff;
}

@media (min-width: 1200px) {
  article div{

  }

  .categoria__image {
    float: left;
    margin: 20px;
  }
}

.show {
  display: block;
  transform: translateY(0px);
}

.list-detalles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  width: 100%;
  list-style: none;
}

.list-detalles li {
  width: 100%;
  border: 1px solid #000;
  border-radius: 5px;
  text-align: center;
}

.list-detalles li a {
  text-align: center;
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  color: #000;
}

.list-detalles li a:hover {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

section {
  padding: 25px 10px;
  border-bottom: 1px solid #000;
}

.read-more {
  padding: 5px 10px;
  margin-top: auto;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
}

.read-more:hover {
  color: var(--container-color);
  background-color: var(--primary-color);
}

.detalles-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  list-style: none;
}

.detalles-links {
}

.detalles-links a {
  display: block;
  text-decoration: none;
  text-align: center;
  background-color: var(--primary-color);
  color: var(--container-color);
  border-radius: 5px;
  padding: 5px 15px;
  font-weight: bold;
}

.detalles-links a:hover {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  background-color: var(--container-color);
}

footer {
  background-color: #ee0000;
  text-align: center;
  padding: 10px;
  font-weight: 600;
  color: #fff;
}

.social-media__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  list-style: none;
  padding-bottom: 20px;
}

.social-media__link {
  font-size: 1.7rem;
  color: #fff;
  padding: 10px;
}

.social-media__link:hover {
  transform: scale(1.2);
}
